import { request } from 'utils';

const prefix = '/api/categories';

export const getPracticeTypes = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: '/PracticeType',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getPracticeTypeById = id =>
  request({
    prefix,
    url: `/PracticeType/GetById`,
    method: 'GET',
    params: { id },
  });

export const deletePracticeType = id =>
  request({
    prefix,
    url: `/PracticeType`,
    method: 'DELETE',
    data: { id },
  });

export const updatePracticeType = ({ id, label, grade, isTotal, isStudy, isShown }) =>
  request({
    prefix,
    url: `/PracticeType`,
    data: { id, label, grade, isTotal, isStudy, isShown },
    method: 'PUT',
  });

export const createPracticeType = ({ id, label, grade, isTotal, isStudy, isShown }) =>
  request({
    prefix,
    url: `/PracticeType`,
    data: { id, label, grade, isTotal, isStudy, isShown },
    method: 'POST',
  });



