import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input, Checkbox, InputNumber } from 'antd';
import { getPracticeTypeById, updatePracticeType, createPracticeType } from 'services/practiceType';
import { getQueryParams } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    label: '',
    grade: 0,
    isTotal: true,
    isStudy: true,
    isShown: true,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTrainingType' })
class FormTrainingType extends Component {
  state = {
    trainingTypeId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ trainingTypeId: queryParamId });
      this.getTrainingById(queryParamId);
    }
  };

  getTrainingById = async id => {
    const res = await getPracticeTypeById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { trainingTypeId } = this.state;
    try {
      if (!trainingTypeId) {
        await createPracticeType(values);
      } else {
        await updatePracticeType({ id: trainingTypeId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { trainingTypeId } = this.state;
    console.log(trainingTypeId);
    return (
      <Card title={trainingTypeId ? 'Cập nhật loại rèn luyện' : 'Thêm loại rèn luyện'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item hasFeedback label="Ký hiệu" {...formItemLayout}>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: '',
                  },
                ],
              })(<Input disabled={trainingTypeId} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên loại RL" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: '',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Điểm tối đa" hasFeedback>
              {getFieldDecorator('grade', {
                initialValue: initialValue.grade,
                rules: [
                  {
                    required: true,
                    message: '',
                  },
                ],
              })(<InputNumber min={0} max={100} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tính tổng điểm">
              {getFieldDecorator('isTotal', {
                initialValue: initialValue.isTotal,
                valuePropName: 'checked',
              })(<Checkbox />)}{' '}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Điểm học tập">
              {getFieldDecorator('isStudy', {
                initialValue: initialValue.isStudy,
                valuePropName: 'checked',
              })(<Checkbox />)}{' '}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Hiển thị">
              {getFieldDecorator('isShown', {
                initialValue: initialValue.isShown,
                valuePropName: 'checked',
              })(<Checkbox />)}{' '}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTrainingType {...props} />
  </Page>
));



